.recomendation-container {
  display: flex;
  height: 170px;
  overflow-x: scroll;
  width: 345px;
}

.recomendation {
  min-width: 44%;
}

.image-details {
  align-self: center;
  width: 80%;
}


.details-container {
  background-color: rgb(242, 240, 201);
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-bottom: 45px;
}

.instructions {
  background-color: rgb(242, 240, 201);
  color: rgb(89, 70, 46);
}

.category-details {
  color: #a69888;
}

.start-recipe-btn {
  bottom: 0;
  height: 30px;
  position: fixed;
  width: 330px;
}
